.about-us {
  margin: 0;
  padding: 0;
  padding-left: 30px;
}
.about-us__title {
  margin: 0;
  padding: 0;
  font-size: 25px;
  line-height: 30px;
}
.about-us__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.about-us__description {
  font-size: 17px;
  line-height: 20px;
  margin-top: 88px;
}
.info {
  margin: 0;
  padding: 0;
}
.info__header p {
  padding: 0;
  margin: 0;
}
.info__title {
  display: flex;
  font-size: 27px;
  line-height: 30px;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 0;
  margin-bottom: 113px;
  width: 260px;
  text-align: center;
}
.info__wrap {
  display: flex;
  margin: 0;
  height: 73px;
  vertical-align: middle;
  background: #63d1bb;
  padding-left: 30px;
  padding-top: 30px;
  width: auto;
}
.info__wrap p {
  margin: 0;
  padding: 0;
}
.info__link {
  display: block;
  text-decoration: none;
  font-size: 19px;
  line-height: 24px;
  color: white;
  width: 105px;
}
.info__wrap--2 {
  background: #56c3b2;
}
.main-nav {
  color: black;
}
.page-footer {
  color: black;
}
.page-header {
  margin-top: 22px;
  margin-bottom: 22px;
}
.page-main__title {
  margin: 0;
  padding: 0;
  font-size: 27px;
  line-height: 30px;
  padding-left: 30px;
  padding-right: 30px;
  background-color: #f1f1f1;
  padding-top: 77px;
  padding-bottom: 81px;
  text-align: center;
  margin-bottom: 30px;
}
.product__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.product__item {
  margin-left: 30px;
  margin-bottom: 64px;
}
.product__logo-image {
  margin-bottom: 31px;
}
.product__name {
  margin: 0;
  margin-bottom: 32px;
  padding: 0;
  font-size: 17px;
  line-height: 24px;
}
.product__description {
  margin: 0;
  margin-bottom: 58px;
  padding: 0;
  font-size: 13px;
  line-height: 30px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #444444;
}
.product__price {
  text-decoration: none;
  color: black;
  margin: 0;
  padding: 0;
  font-size: 15px;
  line-height: 24px;
}
.product__link {
  text-decoration: none;
}
.site-list {
  color: black;
}
a {
  text-decoration: none;
  color: black;
  font-size: 17px;
  line-height: 24px;
}
.social {
  color: black;
}
.top-item {
  margin: 0;
  padding: 0;
  margin-left: 30px;
}
.top-item p {
  margin: 0;
  padding: 0;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
}
.top-item__header {
  margin: 0;
  padding: 0;
  margin-top: 34px;
}
.top-item__title {
  font-size: 25px;
  line-height: 30px;
  margin: 0;
  padding: 0;
  margin-bottom: 14px;
}
.top-item__description {
  margin: 0;
  padding: 0;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  margin-bottom: 35px;
}
.top-item__list {
  font-size: 15px;
  line-height: 20px;
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 0;
  padding: 0;
  margin-top: 35px;
  margin-left: -2px;
  margin-bottom: 29px;
}
.top-item__list tr {
  width: 260px;
  margin: 0;
  padding: 0;
  height: 41px;
  padding-top: 17px;
  display: block;
  border-bottom: 1px solid #ececec;
  text-align: left;
}
.top-item__name {
  width: 98px;
}
.top-item__image {
  padding: 0;
  margin-bottom: 30px;
}
.top-item__price {
  display: inline-block;
  width: 249px;
  text-align: center;
  font-size: 17px;
  line-height: 24px;
  margin: 0;
  padding: 0;
}
.top-item__order {
  margin: 0;
  padding: 0;
  font-size: 17px;
  line-height: 20px;
  margin-bottom: 76px;
}
.user-lsit {
  color: black;
}
.button {
  width: 259px;
  border: 2px solid black;
  color: black;
  text-decoration: none;
  text-transform: uppercase;
  display: flex;
  padding-top: 18px;
  padding-bottom: 18px;
  flex-direction: column;
  text-align: center;
  vertical-align: middle;
  position: relative;
  background: #ffffff;
}
@font-face {
  font-family: "Open Sans Bold";
  src: url("../fonts/opensansbold.woff2") format("woff2");
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2");
}
body {
  width: 320px;
  padding: 0;
  margin: 0 auto;
  font-family: "Open Sans Bold", "Arial", sans-serif;
  color: #444444;
}
body h2,
body li {
  color: black;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
